  cursor: url("../img/hover-paws_light.svg"), pointer;
}
.btn__first {
  border: 2px solid var(--color-dark);
  border-radius: 30px;
  padding: 25px 45px;
  font-size: 20px;
  text-transform: uppercase;
  background: #fff;
  transition: var(--anim2);
}
