  }
}
@media (hover: hover) {
  .btn__first:hover {
    color: var(--color-first);
    background: var(--color-dark);
  }
}
.btn__second {
  border-radius: 30px;
  padding: 15px 30px;
  font-size: 12px;
  text-transform: uppercase;
  background: #fff;
  transition: var(--anim2);
}
@media (hover: hover) {
  .btn__second:hover {
    color: var(--color-first);
    background: var(--color-dark);
  }
}
.btn__third {
  display: flex;
