  }
}
@media (hover: hover) {
  .btn__third:hover {
    background: var(--color-white);
    cursor: url("../img/hover-paws_dark.svg"), pointer;
  }
}
.btn-list {
  position: relative;
  z-index: 5;
  height: min-content;
}
.btn-list--top .no-list {
  top: unset;
  bottom: 80%;
}
@media (hover: hover) {
  .btn-list--top:hover .no-list {
    bottom: 100%;
    opacity: 1;
    visibility: visible;
    top: unset;
  }
}
.btn-list ul {
  position: absolute;
  top: 80%;
  z-index: -1;
  display: flex;
  flex-direction: column;
  border-radius: 30px;
  padding: 30px;
  color: var(--color-white);
  background: var(--color-dark);
  opacity: 0;
  visibility: hidden;
  transition: var(--anim2);
  gap: 15px;
}
@media (hover: hover) {
  .btn-list:hover ul {
    top: 100%;
    opacity: 1;
    visibility: visible;
  }
}

.btn--burger {
  display: none;
}
@media screen and (max-width: 1023px) {
  .btn--burger {
    --line-offset: 2px;
    --color: var(--color-first);
    --color-active: var(--color-first);
    position: relative;
    z-index: 15;
    display: flex;
    width: 36px;
    height: 24px;
    cursor: pointer;
  }
  .btn--burger span {
    position: absolute;
    display: block;
    margin-bottom: 2px;
    width: 100%;
    height: 2px;
    background: var(--color);
    transition: 0.4s;
  }
  .btn--burger span:nth-child(1) {
    top: 0;
  }
  .btn--burger span:nth-child(2) {
    top: 50%;
    transform: translateY(-50%);
  }
  .btn--burger span:nth-child(3) {
    top: 100%;
    transform: translateY(-100%);
  }
  .btn--burger.active span {
    top: 50%;
    background: var(--color-active);
    transform: translateY(-50%);
  }
  .btn--burger.active span:nth-child(1) {
    transform: translateY(-50%) rotate(45deg);
  }
  .btn--burger.active span:nth-child(2) {
    opacity: 0;
  }
  .btn--burger.active span:nth-child(3) {
    transform: translateY(-50%) rotate(-45deg);
  }
}

.container {
